.material-card {
  position: relative;
  height: 0;
  padding-bottom: calc(100% - 16px);
  margin-bottom: 6.6em;
}
.material-card h2 {
  position: absolute;
  top: calc(100% - 16px);
  left: 0;
  width: 100%;
  padding: 10px 16px;
  color: #fff;
  font-size: 1.2em;
  line-height: 1.6em;
  margin: 0;
  z-index: 10;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  background-color: #e34f26;
}
.material-card h2:before, .material-card h2:after {
  content: " ";
  position: absolute;
  left: 0;
  top: -16px;
  width: 0;
  border: 8px solid;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.material-card h2:after {
  top: auto;
  bottom: 0;
  border-bottom-color: transparent;
  border-left-color: transparent;
  border-top-color: #e34f26;
  border-right-color: #e34f26;
}
.material-card h2:before {
  border-top-color: transparent;
  border-left-color: transparent;
  border-right-color: rgba(227, 79, 38, 0.5803921569);
  border-bottom-color: rgba(227, 79, 38, 0.5803921569);
}
